.navbar {
  background-color: transparent; /* Transparent navbar */
  color: black;
  padding: 10px 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  box-shadow: 0 3px 5px rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.navbar-logo {
  display: flex;
  align-items: center;
}

.logo-image {
  height: 50px;   /* Adjust logo size */
  width: auto;
  object-fit: contain;
}

.navbar-links {
  display: flex;
  gap: 10px; /* Closer spacing */
}

.navbar-links a {
  color: black;
  text-decoration: none;
  border: 1px solid black; /* Thinner border */
  padding: 6px 10px; /* Smaller button */
  font-size: 13px; /* Slightly smaller font */
  font-weight: 500;
  text-transform: uppercase;
  transition: 0.3s ease-in-out;
  border-radius: 3px;
}

.navbar-links a:hover {
  background-color: black;
  color: white;
}

.menu-icon {
  display: none;
  font-size: 24px;
  cursor: pointer;
}

/* Responsive Navbar */
@media (max-width: 768px) {
  .menu-icon {
      display: block;
  }

  .navbar-links {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 60px;
      right: 0;
      background: white;
      width: 180px;
      padding: 10px;
      border-radius: 5px;
      box-shadow: 0 3px 5px rgba(255, 255, 255, 0.2);
  }

  .navbar-links.active {
      display: flex;
  }

  .navbar-links a {
      padding: 10px;
      font-size: 14px;
  }
}
